import React from 'react'
import { Button, Col, Form, Row } from 'react-bootstrap'

const AttachPatientReport = () => {
  return (
    <>
      <>
      <Row className="mb-3">
        <Form.Group
          as={Col}
          className="d-flex offset-md-7 col-md-5 col-sm-12 col-sm-12"
          controlId="formGridEmail"
        >
          <Form.Control
            type="text"
            className="me-2"
            placeholder="Patient Mobile Number"
          />
          <Button>Search</Button>
        </Form.Group>
      </Row>
    </>
    </>
  )
}

export default AttachPatientReport